import React, { useEffect, useState } from "react";
import "./Header.css";
/* import "./HeaderMobile.scss";
import '~nprogress/nprogress'; */
import SearchIcon from "@material-ui/icons/Search";
import ShoppingBasketIcon from "@material-ui/icons/ShoppingBasket";
import { Link } from "react-router-dom";
import { useStateValue } from "./StateProvider";
import { auth } from "./firebase";


function Header() {
    const [{ basket, user, totalAmount }, dispatch] = useStateValue();

    const handleAuthentication = () => {
        if (user) {
            auth.signOut()
        }
    }

    // useEffect(() => { 

    //     basket.length != 0 && basket.reduce((item, prev) => {
    //         console.log(item, prev)
    //     }
    //     )
    //     console.log('ol')
    // })




    return (
        <div className='header'>
            <Link to='/'>
                <img className="header__logo" src="http://pngimg.com/uploads/amazon/amazon_PNG11.png" alt="" />
            </Link>

            <div className="header__search">
                <input className="header__searchInput" type="text" />
                <SearchIcon className="header__searchIcon" />
                {/*                 <SearchIcon className="header__searchIcon" />*/}
            </div>

            <div className="header__nav">
                <Link to={!user && '/login'}>
                    <div onClick={handleAuthentication} className="header__option">
                        <span className="header__optionLineOne">Hello {!user ? 'Guest' : user.email}</span>
                        <span className="header__optionLineTwo">{user ? 'Sign Out' : 'Sign In'}

                        </span>
                    </div>
                </Link>
                <Link to='/checkout'>
                    <div className="header__optionBasket">
                        <ShoppingBasketIcon />
                        <span className="header__optionLineTwo header__basketCount">{totalAmount}
                        </span>
                    </div>
                </Link>

            </div>
        </div>

    )
}

export default Header
